* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  height: var(--vhGrid, 100vh);
}

body {
  width: var(--vwGrid, 100vh);
  max-width: var(--vwGrid, 100vh);
  height: var(--vhGrid, 100vh);
  font-size: .8em;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: 'Open Sans', sans-serif;
  color: var(--snap-text-dark);
  margin: auto;
  background-color: var(--snap-main-background);
}

button {
  width: 100%;
  letter-spacing: .03rem;
}

button[disabled] {
  opacity: .38; /* why  38%? google material design */
  pointer-events: none;
}

input[type="text"] {
  padding: .5rem;
  width: 100%;
  border-color: var(--snap-border-lightest);
  border-style: solid;
}

figure {
  margin: .5rem 0 0;
}

a:hover {
  text-decoration: none;
}

ul {
  margin: 0;
  padding: 0;
}

li {
  list-style-type: none;
  margin: 0 0 .5rem 0;
}

progress {
  margin: 0 !important;
  width: 100%;
  border: 0;
  background: var(--snap-progress-bg);
  max-height: 5px;
}

progress::-webkit-progress-bar {
  background-color: var(--snap-progress-bg);
}
progress::-webkit-progress-value {
  background-color: var(--snap-text-medium-dark);
}
progress::-moz-progress-bar {
  background-color: var(--snap-progress-bar);
}

.progress-icon {
  color: var(--snap-progress-bar) !important;
  cursor: pointer;
}

.no-progress {
  background-color: var(--snap-progress-bg);
}

.inline-progress-container progress {
  width: 95px;
  height: 3px;
  left: 0;
  top: 2.5rem;
}

hr {
  margin: 0;
}

.font-size-small {
  font-size: .7rem;
}

.desaturate {
  opacity: .6;
  filter: grayscale(80%);
}

.no-progress {
  background-color: var(--snap-progress-bg);
}



.btn-collection {
  background-color: var(--snap-portrait);
  color: var(--snap-text-dark);
}

.btn-language {
  background-color: var(--snap-landscape);
  color: var(--snap-text-medium-dark);
}

.btn.btn-non-taxa {
  width: 15rem;
  background-color: var(--snap-non-taxa);
  float: left;
}

.non-taxa-definition {
  padding: 0 0.5rem;
}

.icon::after
{
  display: inline-block;
  font-style: normal;
  font-variant: normal;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
  font-family: "Font Awesome 5 Free";
}

.icon.active::after {
  font-weight: 900;
  content: "\f00c";
  padding: 0 0.5rem 0 0;
  margin: 0 0 0 -1.5rem;
  color: rgb(99, 127, 91);
}

.icon.choice::after {
  font-weight: 900;
  content: "\f2e7";
  padding: 0 0 0 0.25rem;
  color: rgb(99, 127, 91);
}

.icon.edible::after {
  font-weight: 900;
  content: "\f2e7";
  padding: 0 0 0 0.25rem;
  color: var(--snap-inactive);
}

.icon.inedible::after {
  font-weight: 900;
  content: "\f056";
  padding: 0 0 0 0.25rem;
  color: var(--snap-inactive);
}

.icon.toxic::after {
  font-weight: 900;
  content: "\f071";
  padding: 0 0 0 0.25rem;
  color: var(--snap-alert);
}

.dropdown-menu {
  margin: 0.15rem 0 0 0;
  padding: 0;
  color: var(--snap-text-dark);
  border: .05rem solid var(--snap-border-light);
  left: inherit !important;
}

.non-taxa .dropdown-menu {
  width: 15rem !important;
  background-color: var(--snap-dd-non-taxa-bg-intial);
}

.non-taxa a.dropdown-item.active {
  background-color: var(--snap-dd-non-taxa-bg-active);
}

a.dropdown-item {
  color: var(--snap-text-medium-dark);
  padding: 0.5rem 0 0.5rem 1rem;
  cursor: pointer;
}

.dropdown-menu.show {
  top: -.25rem !important;
}

.non-taxa a.dropdown-item:hover {
  background-color: var(--snap-dd-non-taxa-bg-hover);
}

.underline-external-link {
  color: var(--snap-link) !important;
  text-decoration: none;
  border-bottom: solid .05rem !important;
  color: inherit;
  cursor: pointer;
}

.lesson-title {
  line-height: 1.6;
}

.toUpperCase {
  text-transform: uppercase;
}

.home, .settings, .nav-info, .list, .glossary, .email, .login, .email > a, .language {
  cursor: pointer;
  color: var(--snap-text-dark);
  font-size: 1.4rem;
}

a.icon:hover {
  color: var(--snap-text-light);
}

.email span {
  font-size: .8rem;
}

.glossary svg {
  vertical-align: middle;
}

.grid-header {
  background-color: rgb(153, 172, 149);
  background-color: var(--snap-portrait);
  display: flex;
  align-items: center;
  color: var(--snap-text-dark);
  width: 100%;
  border-radius: 0.25rem 0.25rem 0 0;
  letter-spacing: 0.07rem;
  text-transform: uppercase;
}

.grid-header > div {
  padding: 0 0.5rem;
}

.hover-box {
  border: .05rem solid rgba(48, 94, 37,.8);
  padding: .25rem;
  margin: 0 0 0 -.25rem;
  border-radius: 0.25rem;
}

.nav-icons .active-icon, .active-icon {
  color: white;
}

.nav-icons .inactive-icon {
  color: var(--snap-inactive);
  cursor: not-allowed;
}

.info-box .inactive-icon {
  display: none;
}

.disabled-icon {
  cursor: initial;
  opacity: .38; /* why  38%? google material design */
  pointer-events: none;
}

.scrollable.card {
  text-align: left;
}

.score-summary-container {
  height: calc((var(--vh, 1vh) * 100) - var(--vhFixtures, 15vh));
}

.snapdragon-container {
  justify-content: flex-start;
  position: relative;
}

.test-card-question.standard-block, .lesson-list-header.centred-block {
  border-bottom: .03rem solid var(--snap-border-medium);
}

.home-container {
  position: relative;
  display: grid;
  grid-template-rows: var(--vhStandardBlock) auto;
}

.feature-types {
  background-color: var(--snap-content-background);
}

.species.card {
  margin: 0;
}

.non-taxa {
  display: flex;
  justify-content: space-between;
  margin-top: 1rem;
}

.non-taxa .btn-group {
  width: 100%;
  display: flex;
  align-items: center;
  flex-direction: column;
  margin: 0 .5rem;
  justify-content: space-evenly;
  background-color: var(--snap-landscape);
}

.non-taxa .btn.btn-secondary {
  height: initial;
}

.non-taxa .species-header {
  padding: 0 1.25rem 0 0;
}

.non-taxon.card img {
  height: 4rem;
  width: 4rem;
}

.non-taxon .btn.btn-secondary {
  width: 100%;
  background-color: var(--snap-landscape);
  border-radius: unset;
}

.non-taxa-definition {
  display: flex;
  align-items: center;
  padding: 0 2rem;
}

.non-taxon .definitions {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  height: 100%;
  text-align: left;
}

.species.card-small {
  padding: 0.5rem;
}

.card.radio-buttons-container {
  margin: 0 0 0.5rem 0;
}

.binomial {
  font-style: italic;
  text-transform: initial;
}

.lesson-plans .table-row.species-background:nth-child(n) {
  background-color: var(--snap-species-highlight-background);
}

.lesson-plans .table-row.taxon-background:nth-child(n) {
  background-color: var(--snap-taxon-card-background);
}

.snapdragon-options {
  padding: 0 .5rem;
}

.grid {
  display: grid;
  grid-auto-flow: column;
  width: 100%;
  height: var(--vhGrid, 100vh);
}

.left-grid, .right-grid {
  display: grid;
  grid-template-columns: 100%;
  grid-auto-flow: column;
  grid-template-rows: var(--vhRunningBlock, 7.5vh) 5px auto var(--vhRunningBlock, 7.5vh);
}

.left-body, .right-body {
  display: grid;
  grid-auto-flow: row;
  height: calc((var(--vh, 1vh) * 100) - var(--vhFixtures, 15vh));  
  background-color: var(--snap-content-background);
}

.header, .footer {
  display: flex;
  align-items: center;
  padding: 0 0.5rem;
  letter-spacing: 0.1rem;
  text-transform: uppercase;
  color: var(--snap-text-medium-dark);
}

.header > div:nth-child(2) {
  max-width: 50%;
  font-size: .9rem;  
}

.footer {
  bottom: 0;
}

.left-grid .footer .nav-icons, .right-grid .footer .nav-icons {
  flex: auto;
  justify-content: space-evenly;
}

.left-grid .footer .nav-icons li, .right-grid .footer .nav-icons li {
  margin: 0 !important;
}

.footer-round, .footer-score span {
  color: var(--snap-text-medium-dark);
  padding: 0.75rem;
}

.left-grid .header, .left-grid .footer {
  justify-content: flex-end;
  background-color: var(--snap-portrait);
}

.right-grid .header, .right-grid .footer {
  justify-content: center;
  background-color: var(--snap-landscape);
}

.header-text {
  color: var(--snap-text-dark-medium-dark);
}

.scrollable {
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;  
}

/* .txt-specimen-species {
  margin: 0 0 0 1rem;
} */

.answer {
  padding: 1rem;
  text-transform: capitalize;
  cursor: pointer;
}

.answer button {
  width: 10rem;
}

.squares {
  display: grid;
  grid-auto-flow: row;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr;
}

.tiles {
  display: grid;
  height: 50%;
  grid-auto-flow: column;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr;
}

.squares > div {
  display: grid;
  justify-self: center;
  height: 140px;
  width: 224px;
}

.squares img, .tiles img {
  cursor: pointer;
  height: inherit;
  width: inherit;
  object-fit: cover;
}

.traits.squares img {
  object-fit: scale-down;
}

.traits.squares > div.multiple {
  grid-template-columns: 1fr 1fr;
  cursor: pointer;
  width: 80%;
}

.square, .tile {
  border-radius: 0.2rem;
  cursor: pointer;
}

.strips {
  display: grid;
  grid-template-columns: 1fr 1fr;
  cursor: pointer;
  height: calc( (var(--vh, 1vh) * 100) 
    - var(--vhFixtures, 15vh) 
    - var(--vhStandardBlock, 10vh)
    - var(--vhStandardBlock, 10vh)
    - var(--vhStandardBlock, 10vh)
    - var(--vhButtonBlock, 9vh)
    );
}

.strip {
  background: #fff;
  border: none;
  border-radius: 2px;
  box-shadow: 0 2px 8px rgba(0,0,0,.2);
  margin: .5rem .25rem;
  padding: .75rem;
  display: flex;
  align-items: center;
  justify-content: center;
}

.strip > div::first-letter {
  text-transform: capitalize;
}

  .strip > * {
    pointer-events: none;
  }

  .pointer-events-none {
    pointer-events: none !important;
  }
  
  .pointer-events-initial {
    pointer-events: initial !important;
  }

.strips .strip.snap-success {
  background-color: var(--snap-success-medium);
  box-shadow: 0 2px 8px rgba(0,0,0,.8);
}

.strips .strip.snap-alert {
  background-color: var(--snap-alert-medium);
  box-shadow: 0 2px 8px rgba(0,0,0,.8);
  border: none;
}

.strip.snap-success > div.disabled {
  color: var(--gray-dark) !important;
}

.strip div {
  text-align: center;
}

.names-container .vernacular-name {
  max-width: 12rem;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  display: inline-block;
}

.names-container.definition .vernacular-name {
  margin-top: 1.25rem;
}

.align-left {
  text-align: left;
}

.snapdragon.card {
  border: 0;
}

.snapdragon.card.align-left {
  color: var(--snap-text-medium-dark);
  padding: 0.5rem;
}

.snapdragon.card.align-left .feature-label {
  letter-spacing: 0.03rem;
  width: fit-content;
  display: inline-table;
}

.taxon-highlight {
  background-color: var(--snap-taxon-card-background);
  padding: 0.5rem;
  margin: -0.5rem -0.5rem 0 -0.5rem;
  display: flex;
  justify-content: right;
}

.taxon-highlight > div:nth-child(1) {
  flex: 1;
  align-self: center;
}

.taxon-highlight > div:nth-child(2) {
  align-self: center;
}

.taxon-highlight img {
  height: 5.5rem;
  width: 5.5rem;
  margin: 0 0 0 1rem;
  object-fit: cover;
}

.latin-name {
  font-style: italic;
  font-family: serif;
  text-transform: initial;
}

.common-name {
  font-style: normal;
  text-transform: capitalize;
}

.taxon-header {
  display: flex;
  justify-content: center;
  flex-direction: column;
  text-align: left;
}

.species-header img {
  filter: grayscale(80%);
  object-fit: scale-down;
  max-height: var(--vhStandardBlock, 10vh);
}

.species-header > div:nth-child(1) {
  display: flex;
  flex-direction: column;
  padding: 0.25rem 1rem 0 0;
}

.card-link-container {
  display: flex;
  align-items: center;
  justify-content: center;
}

.card-link-container i {
  font-size: 2rem;
  cursor: pointer;
  color: var(--snap-iconic-icon-header);
}

.card-link-container i:hover {
  color: var(--snap-text-medium-light);
}

.block-container {
  position: relative;
  justify-content: flex-start;
}

.species-common-name {  
  text-transform: capitalize;
}

.data-box-container {
  height: var(--vhCalendar, 11vh);
  display: grid;
  grid-template-columns: 2fr 2fr 3fr;
  grid-gap: .5rem;
  border-bottom: .03rem solid var(--snap-dd-non-taxa-bg-hover);
  font-size: .8rem;
}

.mixed-content-container {
  top: calc(
      var(--vhStandardBlock, 10vh)
    + var(--vhSmallImageCarousel,23vh));
  height: calc(
        var(--vh, 1vh) * 100
      - var(--vhFixtures,15vh)
      - var(--vhSmallImageCarousel,23vh)
      - var(--vhDoubleStandardBlock,20vh)      
      - .5rem);
}

.taxon.card-card .mixed-content-container {
  height: calc(
        var(--vh, 1vh) * 100
      - var(--vhFixtures,15vh)
      - var(--vhDoubleStandardBlock,20vh) 
      - var(--vhCalendar)
      - 5px);
      padding: .5rem;
}

.taxon.card-card .mixed-content-container .centred-block {
  flex-direction: column;
  height: 100%;
  align-items: flex-start;
  justify-content: space-evenly;
}

.taxon.card-card .data-box-container {
  grid-template-columns: 1fr 2fr;
  padding: 0 .5rem;
}

.inat-box .data-box {
  color: var(--snap-text-medium-light);
  height: 100%;
  border-left: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  border-right: 1px solid var(--snap-border-lightest);
  font-size: .8rem
}

.inat-box .data-box > div:nth-child(1), .inat-box .data-box > div:nth-child(2) {
  display: grid;
  grid-template-columns: 2fr 1fr;
  grid-gap: .25rem;
}

.inat-box .data-box > div:nth-child(2) > div:nth-child(1) {
  padding: 0 0.5rem 0 0;
}

.inat-box .data-box > div > div:nth-child(2) > div {
  text-align: center;
}

.taxon-data > span:nth-child(1) {
  min-width: 50%;
}

.data-box .taxon-data:nth-child(1) > span {
  text-transform: uppercase;
} 

.taxon-data {
  display: flex;
  flex-direction: row;
}

.latin {
  font-style: italic;
  font-family: serif;
}

.taxon-members {
  font-style: italic;
}

.family-img {
  filter: grayscale(80%);
}

.family-name {
  text-transform: uppercase;
}

.history div {
  margin: 0 0 1rem 0;
}

.clickable {
  cursor: pointer;
}

.btn {
  font-size: inherit;
  padding: .5rem;
  border: none;
}

.btn:hover {
  opacity: .8;  
}

.btn.focus, .btn:focus {
  box-shadow: none;
}

btn:disabled {
  pointer-events: none;
}

.btn-placeholder.btn {
  color: var(--snap-text-dark);
}

.snap-btn {
  background-color: var(--snap-list-header-background);
  color: var(--snap-text-black);
  letter-spacing: .05rem;
  border: 1px solid var(--snap-portrait);
  padding: .25rem .75rem;
}

.snap-btn:active {
  color: green !important;
}

.snap-btn:focus:not(:focus-visible), .snap-icon-btn:focus:not(:focus-visible) {
  color: var(--snap-youtube-green) !important;
  outline: none;
  border: 1px solid var(--snap-youtube-green);
}

.snap-icon-btn {
  color: var(--snap-text-dark);
  background-color: transparent;
  border: 1px solid transparent;
  padding: 0 .5rem;
}

.snap-icon-btn:focus, .snap-icon-btn:hover {
  color: var(--snap-youtube-green);
  outline: none;
}

.snap-link-btn, .snap-no-link-btn {
  color: var(--snap-text-dark);
  background-color: transparent;
  border: none;
  font-size: .7rem;
  width: intrinsic;           /* Safari/WebKit uses a non-standard name */
  width: -moz-max-content;    /* Firefox/Gecko */
  width: -webkit-max-content; /* Chrome */
}

.snap-no-link-btn {
  pointer-events: none;
}

.snap-action-btn {
  background-color: var(--snap-tertiary-background);
  color: var(--snap-text-light);
  letter-spacing: .05rem;
  border: 1px solid var(--snap-non-taxa) !important;
  padding: .5rem .75rem;
}

.snap-action-btn:focus {
  outline: none;
  color: var(--snap-text-black);
}

.snap-action-btn.snap-success {
  color: var(--snap-text-light) !important;
  background-color: var(--snap-success) !important;
  border: 0.05rem solid var(--snap-success);
}

.snap-action-btn.snap-alert {
  color: var(--snap-text-light) !important;
  background-color: var(--snap-alert) !important;
  border: 0.05rem solid var(--snap-alert);
}

.btn-tertiary {
  color: var(--snap-text-light);
  background-color: var(--snap-tertiary-background) !important;
  border: 1px solid var(--snap-border-light);
  cursor: pointer !important;
}

.btn-tertiary:focus {
  outline: none;
  color: var(--snap-text-black);
}

.dropdown-toggle::after {
  vertical-align: 0.2rem;
  margin-left: 0.2rem;
}

.course-description {
  margin: 1rem 0;
}

.course-description span {
  margin: 0 0 0.5rem 0;
}

.course-description span:nth-child(1) {
  background-color: lightblue;
  padding: 0.25rem;
}

.course-description .snap-alert {
  width: 50%;
}

.species-count {
  vertical-align: super;
}

.clear-lesson button {
  float: left;
}

.collection span {
  padding: 0 0.5rem;
  cursor: pointer;
}

.dropdown div {
  white-space: nowrap;
}

.single-tile {
  display: flex;
  flex-direction: column;
  margin: 2rem 1rem;
}

.image-border {
  text-align: center;
  margin: 0 0 2rem 0;
  border: .05rem solid;
  border-radius: .2rem;
  padding: 1rem;
}

.blocks {
  flex: auto;
  flex-direction: column;
  padding: 2rem;
}

.block {
  font-size: 1rem;
  display: inline-block;
  cursor: pointer;
  width: fit-content;
  background-color: var(--snap-block-light);
  color: var(--snap-text-dark);
  margin: 0 .5rem 0 0;
  padding: .25rem .5rem;
  letter-spacing: .1rem;
  border-radius: .2rem;
  border: .05rem solid var(--snap-text-dark);
}

.pool .block {
  margin: 0 .5rem .5rem 0;
}

.block + hr {
  margin: .5rem 0 0 0;
}

.pool {
  display: flex;
  flex-wrap: wrap;
}

.pool .block.active {
  color: var(--snap-text-light);
  background-color: rgb(128, 128, 128);
}

.pool .block.correct {
  color: var(--snap-text-light);
  background-color: var(--snap-success);
}

span.correct {
  color: rgb(2, 107, 26);
}

.block.snap-success, .block.snap-alert {
  color: var(--snap-text-light) !important;
  background-color: var(--snap-success) !important;
  border: none;
}

.block.snap-alert {
  background-color: var(--snap-alert) !important;
}

.snap-alert-fg {
  color: var(--snap-alert);
}

.btn.snap-alert {
  padding: 0.175rem 0.75rem;
}

.block.white {
  color: var(--snap-text-dark);
}

.blocks button {
  margin: 2rem 0 0 0;
}

.delay-slider {
  margin: 1rem 0;
}

.delay-slider > div input {
  flex: auto;
  margin: 0 .5rem;
}

.response-update {
  flex: initial;
  display: flex;
  flex-direction: row;
  position: absolute;
  bottom: 0.5rem;
  right: .5rem;
  left: .5rem;
}

.action-update {
  width: 100%;
}

.action-update button {
  width: 100%;
}

.answer-container .icon-container {
  position: relative;
  display: inline-block;
}

.answer-container .icon {
  font-size: 1.5rem;
  position: absolute;
  top: -0.9rem;
}

.taxa svg {
  margin: 0 .25rem 0 0;
}

.taxa .si-glyph-mushrooms svg {
  margin: 0;
}

.species-answer {
  display: none;
}

.species-question,
.species-answer,
