.score-summary-row.species {
  background-color: var(--snap-header-background);
  border: none;
  display: flex;
  width: 100%;
}

.score-summary-row.header {
  cursor: pointer;
  background-color: initial;
  font-size: initial;
  letter-spacing: initial;
  background-color: var(--snap-row-background);
}

.score-summary-row > div:nth-child(1), .score-summary-row > div:nth-child(3) {
  width: 5rem;
  min-width: 5rem;
}

.score-summary-row > div:nth-child(1) i, .score-summary-row > div:nth-child(3) i {
  font-size: 1.2rem;
}

.score-summary-row > div {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.score-summary-row > div:nth-child(2) {
  flex: auto;
  align-items: flex-start;
  max-width: 100%;
}

.score-summary-row.in-columns {
  flex-direction: column;
  padding: 0;
}

.score-summary-row.in-columns > div {  
  display: flex;
  flex-direction: row;
  width: 100%;
}

.score-summary-row.in-columns > div > div {
  padding: 1rem 0;
}

.score-summary-row.in-columns > div > div:nth-child(1), .score-summary-row.in-columns > div > div:nth-child(3) {
  width: 6rem;
  display: flex;
  justify-content: center;
}

.score-summary-row.in-columns > div > div:nth-child(2) {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  width: 100%;
  border-bottom: .03rem solid var(--snap-dd-non-taxa-bg-hover);
}

.score-summary-row.in-columns > div > div:nth-child(2) > div {
  font-size: .8rem;
}

.score-summary-row.in-columns > div > div:nth-child(2) > div:nth-child(1) {
  padding: .5rem;
}

.score-summary-row.in-columns div[data-is-true="true"] > div:nth-child(2) {
  color: var(--snap-success);
}

.score-summary-row.in-columns div[data-is-true="true"] > div:nth-child(2) {
  border: solid 1px var(--snap-progress-bar);
  padding: .3rem;
  display: inline-block;
}

.score-summary-row.in-columns div[data-is-wrong-answer="true"] {
  color: var(--snap-alert); 
}

img[data-has-image="false"] {
  display: none;
}

.score-summary-row > div:nth-child(1) i {
  display: none;
}

.score-summary-row > div[data-is-true="true"]:nth-child(1) i:nth-child(1) {
  display: initial;
  color: rgb(52, 105, 23,.5);
  font-size: .8rem;
  font-size: 1.5rem;
}

.score-summary-row > div:nth-child(1)[data-is-true="false"] i:nth-child(2) {
  display: initial;
  color: var(--snap-alert);
  color: rgba(183, 12, 21,.5);
  font-size: 1.5rem;
}

.score-summary-row img {
  width: 98px;
  height: 68px;
}

.score-summary-row.title > span {
  display: flex;
  justify-content: flex-end;
}

.score-summary-footer {
  justify-content: flex-end;
  align-items: flex-start;
}

.concealed {
  opacity: 0;
}

.fa, .fab, .fal, .far, .fas {
  line-height: 1.5 !important;
}

.autocomplete {
  height: 5rem !important;
  position: absolute !important;
}

.login.icon > i:nth-child(2) {
  color: var(--snap-text-light);
}

.login.icon[data-is-logged-in="true"] > i:nth-child(1) {
  display: none;
}

.login.icon[data-is-logged-in="false"] > i:nth-child(2) {
  display: none;
}

.quick-fire {
  position: relative;
  height: calc((var(--vh, 1vh) * 100) - var(--vhFixtures, 15vh));
  display: flex;
  flex-direction: column;
}

/* .quick-fire .score {
  bottom: 5rem;
  position: absolute;
} */

.quick-fire .strips {
  height: initial;
}

input[type="number"].big {
  width: 4rem;
  padding: .5rem;
}

input[type="number"].medium {
  width: 3rem;
  padding: .25rem;
  font-size: .8rem;
}

.quick-fire-taxa {
  column-count: 2;
}

.quick-fire label, .quick-fire-taxa label {
  cursor: pointer;
}

.quick-fire-taxa span, .quick-fire-branches span {
  font-size: .6rem;
  vertical-align: super;
  display: inline-block;
  padding: 0 .125rem;
  min-width: 1.5rem;
  text-align: left;
}

.quick-fire-branches span {
  font-size: .6rem;
}

.quick-fire .btn-group {
  display: flex;
  flex-wrap: wrap;
}

.quick-fire .btn-group >.btn {
  background-color: var(--snap-snap-action-btn);
  margin: 0 0 .5rem 0;
  border-radius: .25rem !important;
  letter-spacing: .03rem;
  width: fit-content;
  width: max-content;
  cursor: pointer !important;  
}

.quick-fire .quick-fire-branches.btn-group >.btn {
  background-color: var(--snap-btn-filter-off);
  border: 1px solid var(--snap-border-lightest);
}

.quick-fire .quick-fire-options .btn-secondary {
  border: 1px solid var(--snap-collection-active);
}

.quick-fire .quick-fire-options .btn-secondary:not(:disabled):not(.disabled).active {
  color: var(--snap-text-light);
  background-color: var(--snap-tertiary-background);
  border: 1px solid var(--snap-border-light);
}

.quick-fire .quick-fire-branches .btn-secondary {
  font-size: .7rem;
}

.quick-fire .quick-fire-branches .btn-secondary:not(:disabled):not(.disabled).active {
  font-size: .7rem;
  background-color: var(--snap-btn-filter-on);
  color: var(--snap-text-dark-medium-dark);
  border: 1px solid var(--snap-border-light);
}

.quick-fire .btn {  
  cursor: pointer !important;
}

.quick-fire-text-entry > div{
  width: 100%;
}

.quick-fire .custom-checkbox .custom-control-input:checked ~ .custom-control-label::before {
  background-color: var(--snap-btn-blue);
}

.quick-fire-summary-row.card {
  background-color: var(--snap-row-background);
  text-align: left;
  cursor: pointer;
}

.quick-fire-summary-row .card-header {
  border-bottom: 0;
}

.quick-review-tabs span {
  pointer-events: none;
}

.nav-link {
  color: var(--snap-text-medium-dark);
  letter-spacing: .03rem;
}

.nav-pills .nav-link.active, .nav-pills .show>.nav-link {
  color: var(--snap-text-light);
  color: var(--snap-text-medium-dark);
  background-color: var(--snap-collection-light);
}

.nav-pills .nav-item:nth-child(1) .nav-link.active, .nav-pills .nav-item:nth-child(1) .show>.nav-link {
  background-color: var(--snap-success-medium);
}

.nav-pills .nav-item:nth-child(2) .nav-link.active, .nav-pills .nav-item:nth-child(2) .show>.nav-link {
  background-color: var(--snap-alert-medium);
}

.nav-pills .nav-link {
  border-radius: 0;
  position: relative;
}

.quick-fire .nav-link.active.show {
  display: block;
}

.quick-fire .nav-item a:hover {
  color: var(--snap-text-medium-dark);
  background-color: var(--snap-btn-hover);
}

.quick-fire .nav-item:nth-child(1) a:hover {
  background-color: var(--snap-success-light);
}

.quick-fire .nav-item:nth-child(2) a:hover {
  background-color: var(--snap-alert-light);
}

.quick-fire .accordions-container {
  height: calc(
     (var(--vh, 1vh) * 100) 
    - var(--vhFixtures, 15vh)
    - var(--vhDoubleStandardBlock,20vh)
    - var(--vhStandardBlock,10vh) 
    - 1rem
    );
    top: calc(
        var(--vhDoubleStandardBlock,20vh)
      + var(--vhStandardBlock,10vh) 
    );
}

.correct-answer-color {
  color: var(--snap-success); 
}

.incorrect-answer-color {
  color: var(--snap-alert);
}

.low-relief-block {
  border: 1px solid var(--snap-success-light) !important;
  background-color: var(--snap-species-highlight-background);
}

.horizontal-buttons {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: .5rem;
}

.lesson-spinner {
  font-size: 5rem;
  height: calc((var(--vh, 1vh) * 100) - var(--vhFixtures, 15vh)); 
}

.image-slider-190 img {
  height: 190px;
  min-height: 190px;
}

.lesson-icon{
  width: 98px;
  height: 68px;
  object-fit: none;
}

.location-filter-options {
  padding: .5rem;
  grid-template-rows: 1fr 1fr;
  display: grid;
}

.skip-link {
  position: absolute;
  top: -40px;
  left: 0;
  background: #000000;
  color: white;
  padding: 8px;
  z-index: 100;
}

.skip-link:focus {
  top: 0;
}